import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';

import Map from '../../components/map';
import ImageSlider from '../../components/imageSlider';
import Dog from '../../components/dog';

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';	
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		default:
			break;
	}
}

function getWorkoutIcon(workout) {
	switch (workout.type) {
		case 1:
			return 'fas fa-running';
		case 2:
			return 'fas fa-flag-checkered';
		case 3:
			return 'fas fa-hiking';
		default:
			break;
	}
}

function getWorkoutType(workout) {
	switch (workout.type) {
		case 1:
			return 'Trening';
		case 2:
			return 'Konkurranse';
		case 3:
			return 'Tur';
		default:
			break;
	}
}

const initialState = {
    newDog: {},
};

@observer
class WorkoutDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadWorkout() {
		const { workoutStore } = this.props.stores;
        workoutStore.load(this.props.workout);
    }

	showModal = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const img = e.target.dataset.image;
        const imgXXL = e.target.dataset.imagexxl;
		const thumb = e.target.dataset.thumb;
        const name = e.target.dataset.name;
        const title = e.target.dataset.title;
        const date = e.target.dataset.date;
        this.setState({
            displayModal: true,
            currentImage: img,
            currentImageXXL: imgXXL,
            thumb,
            name,
            title,
            date,
        });
    }

    closeModal = () => {
        this.setState({
            displayModal: false,
        });
    }

    back = () => {
        route(`/workouts/`, true);
    }

    componentWillMount() {
        this.loadWorkout();
    }

	render() {
		const { workoutStore, userStore } = this.props.stores;
		const { workout } = workoutStore;
		const track = userStore.findTrack(workout.track);
		const equipment = userStore.findEquipment(workout.equipment);
		const dogs = userStore.findDogs(workout.dogs);

		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link' onClick={this.back}>
                            <i class='fas fa-arrow-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Trening</h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='d-flex mb-2'>
							<div class={`text-center border rounded-circle imageRounded ${getWorkoutColor(workout)}`} style={workout.image ? `background-image: url("${workout.image.s3SmallLink}");` : ''}>
								{!workout.image && <i class={`${getWorkoutIcon(workout)} text-muted mt-3`} style='font-size: 40px;' />}
							</div>
							<div class='flex-grow-1 pl-3'>
								<h5 class='my-0'>{workout.name}</h5>
								{workout.team && userStore.findTeam(workout.team)}<br />
								<small class='text-muted'>
									{util.isoDate(workout.date, false, false, true)}
								</small>
							</div>
						</div>

						{track && track.gpx && <div style='height: 500px;'>
                            <Map stores={this.props.stores} geojson={track.gpx.geoJSON} gpxInfo={track.gpx.gpxInfo} />
                        </div>}

						<div class='container-fluid mb-2 px-0'>
                            <div class='row'>
                                <div class='col-12'>
                                    <div class='d-flex mx-0'>
                                        {workout.images && <ImageSlider images={workout.images} />}
                                    </div>    
                                </div>
                            </div>
						</div>

						<div class='container-fluid'>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
									<small class='text-muted'><i class='fas fa-clock ml-2' /> Vargihet</small>
									<h1>
										{util.secToHms(workout.duration)}
									</h1>
								</div>
	                        </div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
									<small class='text-muted'><i class='fas fa-tachometer-alt ml-2' /> Gj.fart</small>
									<h3>
										{util.format(workout.speedAvg, 1)} km/t
									</h3>
								</div>
								<div class='col text-center'>
									<small class='text-muted'>Type</small>
									<h3>
										{getWorkoutType(workout)}
									</h3>
								</div>
	                        </div>
							<div class='row mt-2'>
								<div class='col text-center'>
									<small class='text-muted'><i class='fas fa-tools' /> Utstyr</small>
									<h3>
										{equipment}
									</h3>
								</div>
								<div class='col text-center'>
									<small class='text-muted'><i class='fas fa-dog ml-2' /> Hunder</small>
									<h3>
										{workout.dogs && <span>{workout.dogs.length}</span>}
									</h3>
								</div>
							</div>
							<div class='row mt-2'>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-road ml-2' /> Distanse</small>
									<h3>
										{util.format(workout.distanceKm, 0)} km
									</h3>
								</div>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-mountain ml-2' /> Stigning</small>
									<h3>
										{util.format(workout.elevation, 0)} m
									</h3>
								</div>
							</div>
							<div class='row mt-2'>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-weather ml-2' /> Vær</small>
									<h3>
										{workout.weather}
									</h3>
								</div>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-mountain ml-2' /> Temperatur</small>
									<h3>
										{workout.temperature}°C
									</h3>
								</div>
							</div>
						</div>
					</div>

                    <div class='offset-0 col-12 mt-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5>Hundene - {dogs.length} stk</h5>
						</div>
						{dogs && dogs.map(dog => (
							<Dog stores={this.props.stores} dog={dog} />
						))}
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='d-flex'>
							<a class='btn btn-primary mt-5 float-right'
								href={`/workouts/edit/${workout.id}`}
							>
								<i class='fas fa-plus' /> Endre {workout.name}
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default WorkoutDetail;
