import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

@observer
class Dog extends Component {
    render() {
        const { dog } = this.props;
		const { userStore, dogStore } = this.props.stores;
        const workoutSummary = dogStore.findWorkoutSummary(dog.id, 2020) || {};
        // {"0":{"year":2020,"dog":16,"count":1,"distanceKm":9.9,"elevation":202,"duration":0,"speedAvg":0}}
        return (
            <div class='w-100 mb-3'>
                <div class='row'>
                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                        {/* {JSON.stringify(dog, null, 4)} */}
                        <div class={`text-center border rounded-circle imageRounded`} style={dog.image ? `background-image: url("${dog.image.s3SmallLink}");` : ''}>
                            {!dog.image && <i class='fas fa-dog text-muted mt-3' style='font-size: 40px;' />}
                        </div>
                        <div class='flex-grow-1 pl-3 pr-2'
                            style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important;`}
                        >
                            <span class='float-right'>
                                {dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
                                {dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                            </span>
                            <a class='stretched-link' href={`/dogs/${dog.id}`}>
                                <h5>{dog.shortname || dog.name}</h5>
                            </a>
                            {userStore.findTeam(dog.team)}<br />
                            <small class='text-muted'>
                                {dog.chipId && <span class='ml-0'><i class='fas fa-microchip' /> {dog.chipId}</span>}
                                {dog.history && <span class='ml-2'><i class='fas fa-file-signature' /> {dog.history.length}</span>}
                                {dog.images && <span class='ml-2'><i class='fas fa-image' /> {dog.images.length}</span>}

                                {workoutSummary && workoutSummary.distanceKm > 0 && <span class='ml-2'><i class='fas fa-road' /> {util.format(workoutSummary.distanceKm, 0)}km</span>}
                                {workoutSummary && workoutSummary.elevation > 0 && <span class='ml-2'><i class='fas fa-mountain' /> {util.format(workoutSummary.elevation, 0)}m</span>}
                                {workoutSummary && workoutSummary.duration > 0 && <span class='ml-2'><i class='fas fa-clock' /> {util.secToHms(workoutSummary.duration)}</span>}
                                {workoutSummary && workoutSummary.speedAvg > 0 && <span class='ml-2'><i class='fas fa-tachometer-alt' /> {workoutSummary.speedAvg}km/t</span>}
                            </small>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Dog;
