import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';

import Images from '../../components/form/images';
import DogHistory from '../../components/dog/history';
import Modal from '../../components/modal';

function byDate(a, b) {
	if (a.date < b.date){
		return -1;
	}
	if (a.date > b.date){
		return 1;
	}
	return 0;
}

const initialState = {
	showLog: false,
};

@observer
class DogsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
			newHistory: {
				date: util.isoDate(new Date(), false, false, true),
				time: util.isoTime(new Date()),
			},
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadDog() {
		const { dogStore } = this.props.stores;
        dogStore.load(this.props.dog);
    }

	resetLog = () => {
		this.setState({
			newHistory: {
				date: util.isoDate(new Date(), false, false, true),
				time: util.isoTime(new Date()),
			},
			showLog: false,
		});
	}

	toggleLog = () => {
		const { showLog } = this.state;
		this.setState({
            showLog: !showLog,
        });
	}

	addImages = (obj) => {
		// updateField({ id: object.id, field, value: file });
		// console.log('addImages', obj.value);
		if (Array.isArray(obj.value)) {
			const { newHistory } = this.state;
			newHistory.images = obj.value;
			this.setState({ newHistory });
		}
	}

	insertLog = async () => {
		const { newHistory } = this.state;
		const { dogStore } = this.props.stores;
		const { dog } = dogStore;

		if (!newHistory.title && !newHistory.comment) {
			return false;
		}

		newHistory.date = `${newHistory.date} ${newHistory.time}`;
		
		await dogStore.save({
			id: dog.id,
			history: {
				...newHistory,
			},
		});
        this.resetLog();
		this.loadDog();
	}

	showModal = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const img = e.target.dataset.image;
        const imgXXL = e.target.dataset.imagexxl;
		const thumb = e.target.dataset.thumb;
        const name = e.target.dataset.name;
        const title = e.target.dataset.title;
        const date = e.target.dataset.date;
        this.setState({
            displayModal: true,
            currentImage: img,
            currentImageXXL: imgXXL,
            thumb,
            name,
            title,
            date,
        });
    }

    closeModal = () => {
        this.setState({
            displayModal: false,
        });
    }

    back = () => {
        route(`/dogs/`, true);
    }

    componentWillMount() {
        this.loadDog();
    }

	render() {
		const { newHistory, showLog, displayModal, currentImage, currentImageXXL, thumb, name, title, date } = this.state;
		const { dogStore, userStore } = this.props.stores;
		const { dog } = dogStore;
		const hasHistory = dog.history && dog.history.length > 0;
        const workoutSummary2019 = dogStore.findWorkoutSummary(dog.id, 2019) || {};
        const workoutSummary2020 = dogStore.findWorkoutSummary(dog.id, 2020) || {};
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link' onClick={this.back}>
                            <i class='fas fa-arrow-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Hund</h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

				{displayModal && <Modal close={this.closeModal} image={currentImage} imagexxl={currentImageXXL} thumb={thumb} name={name} title={title} date={date} />}

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='d-flex mb-2'>
                            {/* {JSON.stringify(dog, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={dog.image ? `background-image: url("${dog.image.s3SmallLink}");` : ''}>
                                {!dog.image && <i class='fas fa-dog text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3 pr-2'
								style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important;`}
							>
								<span class='float-right'>
									{dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
									{dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
								</span>
								<h5>{dog.name}</h5>
								{dog.shortname}<br />

								<div>
									{dog.chipId && <small><i class='fas fa-microchip' /> {dog.chipId}</small>}
								</div>
                            </div>
                        </div>

						<div class='container-fluid mb-2'>
							<div class='row mt-2 border-top'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-road' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.distanceKm || 0, 0)} km
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-road' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.distanceKm || 0, 0)} km
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.elevation || 0, 0)} m
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.elevation || 0, 0)} m
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-clock' /> 2020-21</small>
                                    <h3>
										{util.secToHms(workoutSummary2020.duration || 0)}
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-clock' /> 2019-20</small>
                                    <h3>
                                        {util.secToHms(workoutSummary2019.duration || 0)}
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2 border-bottom'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.speedAvg || 0, 1)} km/t
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.speedAvg || 0, 1)} km/t
                                    </h3>
                                </div>
                            </div>

	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Født</small>
                                    <h3>
                                        {util.isoDate(dog.birth, false, false, true)}
                                    </h3>
                                </div>
                                {dog.deceased && <div class='col text-center border-left'>
                                    <small class='text-muted'>Død</small>
                                    <h3>
                                        {util.isoDate(dog.deceased, false, false, true)}
                                    </h3>
                                </div>}
                            </div>
	                        <div class='row mt-2'>
								<div class='col text-center'>
                                    <small class='text-muted'>Team</small>
                                    <h3>
                                        {dog.team && userStore.findTeam(dog.team)}
                                    </h3>
                                </div>
							</div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Mor</small>
                                    <h3>
                                        {dog.mother}
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'>Far</small>
                                    <h3>
                                        {dog.father}
                                    </h3>
                                </div>
                            </div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Søsken</small>
                                    <h3>
                                        {dog.siblings}
                                    </h3>
                                </div>
                            </div>
                        </div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='container-fluid border-bottom pb-4'>
							<div class='row'>
								<div class='col px-0'>
									<h5>Bilder av {dog.name}</h5>
								</div>
							</div>
							<div class='row'>
								{dog.images && dog.images.map(img => (
									<div class={`text-center border rounded imageRounded`} style={img ? `background-image: url("${img.s3SmallLink}");` : ''}
										onClick={this.showModal}
										data-image={img.s3LargeLink}
										data-imagexxl={img.s3XXLargeLink}
										data-thumb={dog.image.s3SmallLink}
										data-name={dog.name}
									>					
										&nbsp;
									</div>
								))}
							</div>
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						{showLog && <div class='border-bottom mb-2 py-4 px-5'>
							<h5>Legg til logg</h5>
							<div class='row'>
								<div class='form-group col'>
									<label for='dateInput'>Dato</label>
									<input type='date' class='form-control' id='dateInput' aria-describedby='dateHelp'
										onInput={linkState(this, 'newHistory.date')}
										value={newHistory.date}
										autocomplete='off'
									/>
									<small id='dateHelp' class='form-text text-muted'>Dato for logglinje.</small>
								</div>
								<div class='form-group col'>
									<label for='timeInput'>Klokka</label>
									<input type='time' class='form-control' id='timeInput' aria-describedby='timeHelp'
										onInput={linkState(this, 'newHistory.time')}
										value={newHistory.time}
										autocomplete='off'
									/>
									<small id='timeHelp' class='form-text text-muted'>Tid for logglinje.</small>
								</div>
							</div>
							<div class='form-group'>
								<label for='nameInput'>Tittel</label>
								<input type='text' class='form-control' id='nameInput' aria-describedby='nameHelp'
									onInput={linkState(this, 'newHistory.title')}
									value={newHistory.title}
									autocomplete='off'
								/>
								<small id='nameHelp' class='form-text text-muted'>Tittel på logglinje. Vetrinær besøk, Vaksinering, Syk, Profilbilde, etc.</small>
							</div>
							<div class='form-group'>
								<label for='commentInput'>Innhold</label>
								<input type='text' class='form-control' id='commentInput' aria-describedby='commentHelp'
									onInput={linkState(this, 'newHistory.comment')}
									value={newHistory.comment}
									autocomplete='off'
								/>
								<small id='commentHelp' class='form-text text-muted'>Innholdet til loggen. Skriv mer om hva som skjer.</small>
							</div>
							<div class='form-group'>
								<label for='costInput'>Kostnad</label>
								<input type='text' class='form-control' id='costInput' aria-describedby='costHelp'
									onInput={linkState(this, 'newHistory.cost')}
									value={newHistory.cost}
									autocomplete='off'
								/>
								<small id='costHelp' class='form-text text-muted'>Kostnad for denne hendelsen.</small>
							</div>

							<div class='container-fluid'>
								<div class='row'>
									{newHistory.images && newHistory.images.map(img => (
										<div class={`text-center border rounded imageRounded`} style={img ? `background-image: url("${img.s3SmallLink}");` : ''}>
											&nbsp;
										</div>
									))}
								</div>
							</div>
							<Images object={newHistory} field='images' updateField={this.addImages} />

							<button type='button' class='btn btn-success'
								onClick={this.insertLog}
							>
								Legg til logg
							</button>
							<button type='button' class='btn btn-danger float-right'
								onClick={this.resetLog}
							>
								Nullstill
							</button>
						</div>}

						<div class='container-fluid'>
							<div class='row'>
								<div class='col-12'>
									<a class='btn btn-primary mt-5 float-right'
										href={`/dogs/edit/${dog.id}`}
									>
										<i class='fas fa-edit' /> Endre {dog.name}
									</a>
									<button class='btn btn-secondary mt-5 float-left'
										onClick={this.toggleLog}
									>
										<i class='fas fa-plus' /> {showLog ? 'Skjul logg' : 'Ny logg'}
									</button>
								</div>
							</div>
						</div>
					</div>

					<div class='offset-0 col-12'>
						<div class='container-fluid'>
							<div class='row'>
								<div class='col-12'>
									<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
										<h5 class='mt-3'>Logg for {dog.name}</h5>
									</div>
									{!hasHistory && <div class='text-muted'>Loggen til {dog.name} er tom</div>}
									{hasHistory && dog.history.sort(byDate).reverse().map(line => (
										<DogHistory stores={this.props.stores} line={line} />
									))}							
								</div>
							</div>
						</div>

					</div>
				</div>
			</div>
		);
	}
}

export default DogsDetail;
