import { h, Component } from 'preact';
import { Router, route } from 'preact-router';
import { createHashHistory } from 'history';
import { observer } from 'mobx-preact';

import util from 'preact-util';

import PubSub, { topics } from '../lib/pubsub';

import appState from '../stores/appstate';
import waypointStore from '../stores/waypoint';
import userStore from '../stores/user';
import dogStore from '../stores/dog';
import teamStore from '../stores/team';
import workoutStore from '../stores/workout';
import trackStore from '../stores/track';

import Progress from './progress';
import RefreshSpinner from './refresh';
import ReadyToRefresh from './refresh/ready';
import Header from './header';
import Footer from './footer';
import Login from './login';
import Register from './register';
import Forgotten from './forgotten';

// Code-splitting is automated for routes
import Home from '../routes/home';
import Users from '../routes/users';

import Dogs from '../routes/dogs';
import DogsDetail from '../routes/dogs/detail';
import DogsEdit from '../routes/dogs/edit';

import Workouts from '../routes/workouts';
import WorkoutsDetail from '../routes/workouts/detail';
import WorkoutsEdit from '../routes/workouts/edit';

import Tracks from '../routes/tracks';
import TracksDetail from '../routes/tracks/detail';
import TracksEdit from '../routes/tracks/edit';

import Teams from '../routes/teams';
import TeamsDetail from '../routes/teams/detail';
import TeamsEdit from '../routes/teams/edit';

import Search from '../routes/search';
import Logout from '../routes/logout';

@observer
class App extends Component {
	constructor(props) {
        super(props);
		this.stores = {
            appState,
            waypointStore,
            userStore,
            dogStore,
            teamStore,
            workoutStore,
            trackStore,
        };
        this.state = {
            jwtToken: util.getJwtToken(),
        };
        this.subscriptions();
        const { darkmode } = appState;
        util.toggleDarkModeClasses(darkmode);
    }

    subscriptions() {
        PubSub.subscribe(topics.LOG_OUT, () => {
            this.setState({
                jwtToken: undefined,
            });
            util.removeUserEmail();
            util.removeJwtToken();
        });

        PubSub.subscribe(topics.JWT_TOKEN_CHANGED, async (token) => {
            this.setState({
                jwtToken: token,
            });
            route('/', true);
        });    
    }

	/** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */
	handleRoute = e => {
		this.currentUrl = e.url;
        if (this.appContainer) {
            this.appContainer.scrollTo({ top: 0 });
        }
	};

    async loadAll() {
        appState.getFingerprint();
        await userStore.getInfo();
    }

    touchStart = (e) => {
        this.setState({
            startY: e.touches[0].pageY,
        })
    }

    touchEnd = () => {
        this.setState({
            startY: undefined,
            readyToRefresh: false,
        })
    }

    touchMove = (e) => {
        const { startY, refreshing } = this.state;
        const refreshRange = 100;
        const readyTorefreshRange = 50;
        const y = e.touches[0].pageY;
        const { scrollTop } = this.appContainer;
        if (scrollTop === 0 && y > startY + refreshRange && !refreshing) {
            this.setState({
                refreshing: true,
                readyToRefresh: false,
            });
            this.refreshPage();
        } else if (scrollTop === 0 && y > startY + readyTorefreshRange && !refreshing) {
            this.setState({ readyToRefresh: true });
        }
    }

    doneRefreshing() {
        this.setState({
            startY: undefined,
            refreshing: false,
            readyToRefresh: false,
        });
    }

    refreshPage() {
        switch (this.currentUrl) {
            case '/':
                return this.refreshFrontpage();
            case '/workouts':
            case '/workouts/':
                return this.refreshWorkouts();
            case '/dogs':
            case '/dogs/':
                return this.refreshDogs();
            case '/teams':
            case '/teams/':
                return this.refreshTeams();
            case '/tracks':
            case '/tracks/':
                return this.refreshTracks();
            default:
                console.log(this.currentUrl);
                return this.doneRefreshing();
        }        
    }

    async refreshFrontpage() {
        await userStore.getFeed();
        this.doneRefreshing();
    }

    async refreshWorkouts() {
        await workoutStore.load();
        this.doneRefreshing();
    }

    async refreshDogs() {
        await dogStore.load();
        this.doneRefreshing();
    }

    async refreshTeams() {
        await teamStore.load();
        this.doneRefreshing();
    }

    async refreshTracks() {
        await trackStore.load();
        this.doneRefreshing();
    }


    componentDidMount() {
        const inputProps = util.collectPropsFromElement(window.document.body);
        const apiServer = inputProps.apiServer || `${window.location.protocol}//${window.location.host}`;
        util.setApiServer(apiServer);
        this.loadAll();
    }

	render() {
        const { refreshing, jwtToken, readyToRefresh } = this.state;
		if (!jwtToken) {
            return (
				<div id='app'>
                    <Progress />
                    <Router history={createHashHistory()}>
                        <Login stores={this.stores} {...this.props} path="/" />
                        <Login stores={this.stores} {...this.props} path="/:email" />
                        <Login stores={this.stores} {...this.props} path="/link/login/:logintoken" />

					    <Register stores={this.stores} path="/register/" />
					    <Register stores={this.stores} path="/register/:email" />

					    <Forgotten stores={this.stores} path="/forgotten/" />
					    <Forgotten stores={this.stores} path="/forgotten/:email" />
                    </Router>
				</div>
			);		
        }
		return (
			<div id='app'
                style={`overflow-x: auto; overflow-y: auto; height: 100%; ${readyToRefresh ? 'padding-top: 50px;' : ''} transition: .5s; overscroll-behavior: contain;`}
                onTouchstart={this.touchStart}
                onTouchend={this.touchEnd}
                onTouchmove={this.touchMove}
                ref={c => this.appContainer = c}
            >
                <Progress />
                {readyToRefresh && <ReadyToRefresh />}
                {refreshing && <RefreshSpinner />}

				<Header stores={this.stores} />

				<Router onChange={this.handleRoute} history={createHashHistory()}>
					<Home stores={this.stores} path="/" />

					<Users stores={this.stores} path="/users/" user="me" />
                    <Users stores={this.stores} path="/users/:user" />
                    <Users stores={this.stores} path="/link/verify/:verifyEmailToken" />

					<Dogs stores={this.stores} path="/dogs/" />
					<DogsDetail stores={this.stores} path="/dogs/:dog" />
					<DogsEdit stores={this.stores} path="/dogs/edit/:dog" />

					<Workouts stores={this.stores} path="/workouts/" />
					<WorkoutsDetail stores={this.stores} path="/workouts/:workout" />
					<WorkoutsEdit stores={this.stores} path="/workouts/edit/:workout" />

					<Tracks stores={this.stores} path="/tracks/" />
					<TracksDetail stores={this.stores} path="/tracks/:track" />
					<TracksEdit stores={this.stores} path="/tracks/edit/:track" />

					<Teams stores={this.stores} path="/teams/" />
					<TeamsDetail stores={this.stores} path="/teams/:team" />
					<TeamsEdit stores={this.stores} path="/teams/edit/:team" />

					<Search stores={this.stores} path="/search/:query" />
					<Logout stores={this.stores} path="/logout/" />
				</Router>
                <Footer stores={this.stores} />
			</div>
		);
	}
}

export default App;
