import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Modal from '../modal';
import ImageSlider from '../imageSlider';

@observer
class DogHistoryLine extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showDate: false,
        };
    }

    showModal = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const img = e.target.dataset.image;
        const imgXXL = e.target.dataset.imagexxl;
        const thumb = e.target.dataset.thumb;
        const name = e.target.dataset.name;
        const title = e.target.dataset.title;
        const date = e.target.dataset.date;
        this.setState({
            displayModal: true,
            currentImage: img,
            currentImageXXL: imgXXL,
            thumb,
            name,
            title,
            date,
        });
    }

    closeModal = () => {
        this.setState({
            displayModal: false,
        });
    }

    toggleDate = () => {
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }

    render() {
        const { displayModal, currentImage, currentImageXXL, showDate, thumb, name, title, date } = this.state;
        const { line } = this.props;
        const hasImage = line.images && line.images.length > 0;
        return (
            <div class='w-100 mb-3'>
                <div class='row'>
                    {displayModal && <Modal close={this.closeModal} image={currentImage} imagexxl={currentImageXXL} thumb={thumb} name={name} title={title} date={date} />}

                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                        {/* {JSON.stringify(dog, null, 4)} */}
                        {line.dogImage ? <div class={`text-center border rounded-circle imageRounded imageRoundedSmall`} style={line.dogImage ? `background-image: url("${line.dogImage.s3SmallLink}");` : ''}>
                            &nbsp;
                        </div> : <div class={`text-center border rounded-circle imageRounded imageRoundedSmall`} style={line.images && line.images[0] ? `background-image: url("${line.images[0].s3SmallLink}");` : ''}>
                            {!hasImage && <i class='fas fa-rss text-muted mt-2' style='font-size: 20px;' />}
                        </div>}
                        <div class='flex-grow-1 pl-3 pr-2' style='line-height: 1.0em;'>
                            {line.dogId && <a class='stretched-link' href={`/dogs/${line.dogId}`}>
                                <strong class='mr-2'>{line.dogShortname || line.dogName}: </strong>
                            </a>}
                            {line.title}<br />
                            <small class='text-muted'>
                                {line.date && <small onClick={this.toggleDate}>
                                    {showDate ? util.formatDate(line.date, { locale: 'no-NB' }) : util.formatDistance(line.date, new Date(), { locale: 'no-NB' })}
                                </small>}
                            </small>
                        </div>
                    </div>

                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mt-1'>
                        <div class='row'>
                            <div class='col-12'>
                                <span class='font-weight-lighter'>{line.comment}</span>
                                <span class='float-right'>
                                    {line.cost ? `Kr. ${util.format(line.cost, 2)}` : ''}
                                </span>
                            </div>
                        </div>
                        <div class='row'>
                            <div class='col-12'>
                                <div class='d-flex mx-0'>
                                    <ImageSlider images={line.images} />
                                </div>    
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default DogHistoryLine;
